"use client";

import { useRef, useState, useEffect } from "react";
import { Canvas, useFrame, useThree } from "@react-three/fiber";
import { OrbitControls, useGLTF, Environment, Float } from "@react-three/drei";
import { gsap } from "gsap";

// Duck model component
function Duck(props) {
  const { nodes, materials } = useGLTF("/assets/Duck.glb");
  const duckRef = useRef();
  const { viewport } = useThree();

  // Track mouse position
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });

  useEffect(() => {
    // Mouse move handler
    const handleMouseMove = (event) => {
      // Calculate normalized mouse position
      setMousePosition({
        x: (event.clientX / window.innerWidth) * 2 - 1,
        y: -(event.clientY / window.innerHeight) * 2 + 1,
      });
    };

    window.addEventListener("mousemove", handleMouseMove);

    // GSAP animation on load
    gsap.from(duckRef.current.rotation, {
      y: Math.PI * 2,
      duration: 2,
      ease: "elastic.out(1, 0.3)",
    });

    gsap.from(duckRef.current.position, {
      y: -2,
      duration: 1.5,
      ease: "bounce.out",
    });

    return () => {
      window.removeEventListener("mousemove", handleMouseMove);
    };
  }, []);

  // Animate duck based on mouse position
  useFrame(() => {
    // Smooth rotation based on mouse position
    gsap.to(duckRef.current.rotation, {
      x: mousePosition.y * 0.2,
      y: mousePosition.x * 0.5,
      duration: 0.5,
    });

    // Subtle floating animation
    duckRef.current.position.y = Math.sin(Date.now() * 0.001) * 0.1;
  });

  return (
    <group ref={duckRef} {...props} dispose={null}>
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.LOD3spShape.geometry}
        material={materials.blinn3}
        rotation={[Math.PI / 2, 0, 0]}
        scale={0.5}
      />
    </group>
  );
}

// Main scene component
function Scene() {
  return (
    <>
      <ambientLight intensity={0.5} />
      <spotLight
        position={[10, 10, 10]}
        angle={0.15}
        penumbra={1}
        intensity={1}
        castShadow
      />
      <Float speed={2} rotationIntensity={0.5} floatIntensity={0.5}>
        <Duck position={[0, 0, 0]} />
      </Float>
      <Environment preset="sunset" />
      <OrbitControls enableZoom={false} enablePan={false} />
    </>
  );
}

// Main component
export default function ThreeDLanding() {
  const containerRef = useRef();
  const [dimensions, setDimensions] = useState({ width: 0, height: 0 });

  useEffect(() => {
    // Set initial dimensions
    if (containerRef.current) {
      setDimensions({
        width: containerRef.current.clientWidth,
        height: containerRef.current.clientHeight,
      });
    }

    // Handle resize
    const handleResize = () => {
      if (containerRef.current) {
        setDimensions({
          width: containerRef.current.clientWidth,
          height: containerRef.current.clientHeight,
        });
      }
    };

    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return (
    <div
      ref={containerRef}
      className="relative w-full h-screen bg-gradient-to-b from-indigo-900 to-purple-900"
    >
      <div className="absolute inset-0 z-10 flex flex-col items-center justify-center text-white p-4">
        <h1 className="text-5xl md:text-7xl font-bold mb-4 text-center">
          Interactive 3D Experience
        </h1>
        <p className="text-xl md:text-2xl max-w-2xl text-center mb-8">
          Move your cursor to interact with the 3D model
        </p>
        <button className="px-8 py-3 bg-white text-purple-900 rounded-full font-bold text-lg hover:bg-opacity-90 transition-all transform hover:scale-105">
          Explore More
        </button>
      </div>

      <Canvas shadows camera={{ position: [0, 0, 5], fov: 50 }}>
        <Scene />
      </Canvas>
    </div>
  );
}
